// eslint-disable-next-line
import React, { useState, useEffect } from "react";
import Card from "../components/Card";
import Navbar from "./Navbar";

export default function Search({ query }) {
  const [searchResults, setSearchResults] = useState([]);

  useEffect(() => {
    fetch("https://spotiday.herokuapp.com/search-music/?q=" + query)
      .then((src) => src.json())
      .then((data) => {
        setSearchResults(data);
      });
  }, [setSearchResults, query]);
  useEffect(() => {
    let exampleModal = document.getElementById("exampleModal");
    exampleModal.addEventListener("show.bs.modal", async function (event) {
      let button = event.relatedTarget;
      let title = button.getAttribute("data-bs-title");
      let artist = button.getAttribute("data-bs-artist");
      let thumb = button.getAttribute("data-bs-thumb");
      let id = button.getAttribute("data-bs-id");

      let modalTitle = exampleModal.querySelector(".modal-title");
      let spanTitle = exampleModal.querySelector("#this-title");
      let spanArtist = exampleModal.querySelector("#this-artist");
      let imgThumb = exampleModal.querySelector(".img-thumbnail");
      let audioSrc = exampleModal.querySelector("#this-audio");
      let lowQuality = exampleModal.querySelector("#this-low");
      let highQuality = exampleModal.querySelector("#this-high");
      let flacQuality = exampleModal.querySelector("#this-flac");

      modalTitle.textContent = title;
      spanTitle.textContent = title;
      spanArtist.textContent = artist;
      imgThumb.setAttribute("src", thumb);
      imgThumb.setAttribute("alt", title);

      let srcLow = await fetch(
        `https://spotiday.herokuapp.com/downloadMusic/?id=${id}&quality=low`
      );
      let jsonLow = await srcLow.json();
      audioSrc.setAttribute("src", jsonLow.preview);
      lowQuality.setAttribute("href", jsonLow.download);

      let srcHigh = await fetch(
        `https://spotiday.herokuapp.com/downloadMusic/?id=${id}&quality=high`
      );
      let jsonHigh = await srcHigh.json();
      highQuality.setAttribute("href", jsonHigh.download);

      let srcFlac = await fetch(
        `https://spotiday.herokuapp.com/downloadMusic/?id=${id}&quality=flac`
      );
      let jsonFlac = await srcFlac.json();
      flacQuality.setAttribute("href", jsonFlac.download);
    });
  });
  return (
    <React.Fragment>
      <Navbar placeholder={query} />
      <div className="container">
        <div className="row">
          {searchResults.map((items) => (
            <Card
              key={items.id}
              src={items.thumb}
              title={items.title}
              artist={items.penyanyi}
              id={items.id}
            />
          ))}
        </div>
      </div>
      <div
        className="modal fade"
        id="exampleModal"
        tabIndex="-1"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="exampleModalLabel"></h5>
              <button
                type="button"
                className="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <div className="modal-body">
              <img className="img-thumbnail" alt="y" />
              <p>
                <strong>Title</strong> :{" "}
                <span
                  className="font-normal ease-in-out"
                  id="this-title"
                ></span>
              </p>
              <p>
                <strong>Artist</strong> :{" "}
                <span
                  className="font-normal ease-in-out"
                  id="this-artist"
                ></span>
              </p>
              <p>
                <strong>Preview</strong> :{" "}
                <audio controls="controls" className="mt-2">
                  <source id="this-audio" type="audio/mp3"></source>
                </audio>
              </p>
              <div className="container mt-3">
                <div className="row">
                  <div className="w-1/3">
                    <a className="btn btn-warning" id="this-low" href="#">
                      Download Low Quality
                    </a>
                  </div>
                  <div className="w-1/3">
                    <a className="btn btn-warning" id="this-high" href="#">
                      Download High Quality
                    </a>
                  </div>
                  <div className="w-1/3">
                    <a className="btn btn-warning" id="this-flac" href="#">
                      Download Flac Quality
                    </a>
                  </div>
                </div>
              </div>
            </div>
            <div className="modal-footer">
              <button
                type="button"
                className="btn btn-secondary"
                data-bs-dismiss="modal"
              >
                Close
              </button>
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
}
