import React from "react";
import Navbar from "../components/Navbar";

export default function Dashboard() {
  return (
    <React.Fragment>
      <Navbar />
      <div className="container">
        <div className="row">
          <div className="w-full lg:w-1/2">
            <div className="card bg-warning">
              <div className="card-header">
                <strong className="mr-4 italic hover:shadow-outline font-medium text-xl tracking-wide">
                  Spotiday
                </strong>
              </div>
              <div className="card-body">
                <p className="text-break font-semibold">
                  Halo, ini adalah Website Spotiday download music yang kamu mau
                  disini dengan kualitas terbaik!
                </p>
                <br />
                <span className="mr-4 hover:shadow-outline font-medium text-xl tracking-wide">
                  Bug :{" "}
                </span>
                <ul>
                  <li>
                    Jika pada saat download , url undefined silahkan close modal
                    dan buka lagi
                  </li>
                  <li>Quality FLAC not working</li>
                </ul>
              </div>
              <div className="card-footer">
                <strong>Jangan khawatir akan segera di perbaiki!</strong>
                <p className="text-break">
                  Jika menemukan bug yang lain contact saya melalui instagram :{" "}
                  <a href="//instagram.com/akurizqii" className="alert-link">@akurizqii</a>
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
}
